import React, { useRef, useEffect } from 'react';
import {  select, line, axisBottom, axisRight, scaleLinear } from 'd3';

import Legend from '../Legend/Legend'

import { useResizeObserver } from '../../hooks/useResizeObserver'

const LineGraph = ({ lineGraphData, yAxisMax, reverseData, xAxisFontSize }) => {

    const svgRef = useRef()
    const wrapperRef = useRef()
    const dimensions = useResizeObserver(wrapperRef)

    // will be called initially and then every time the data array changes
    useEffect(() => {
    const svg = select(svgRef.current)

    if (!dimensions || lineGraphData === undefined || xAxisFontSize === undefined) return
    
    let keys = Object.keys(lineGraphData)
    let values = Object.values(lineGraphData)

    if (reverseData !== undefined && reverseData === true) {
        keys = keys.reverse()
        values = values.reverse()
    }

    const xScale = scaleLinear()
        .domain([0, keys.length - 1])
        .range([0, dimensions.width])

    const yScale = scaleLinear()
        .domain([0, yAxisMax])
        .range([dimensions.height, 0])

    const xAxis = axisBottom(xScale)
        .ticks(keys.length)
        .tickFormat(index => keys[index])

    const displayTooltip = (data) => {
        const tooltip = document.getElementById("lineTooltip");
        tooltip.addEventListener("mousemove", (e) => {
            const x = e.clientX
            const y = e.clientY
            tooltip.style.left = (x - 300) + "px"
            tooltip.style.top = (y - 80) + "px"
        })
        const text = document.getElementById("pointInfo")
        text.textContent = setTooltipText(data)
    }

    const setTooltipText = data => {
        console.log(data)
        return 'hello'
        // const targets = feature.properties.targets
        // const formattedProperty = property.replace(/([a-z0-9])([A-Z])/g, '$1 $2').toLowerCase()
        // let targetsText = ""
        // let number = 0
        // if (targets !== 0) {
        //     targetsText = `${targets[selectedTargetIndex].target.replace(/([a-z0-9])([A-Z])/g, '$1 $2').toLowerCase()}`
        //     number = targets[selectedTargetIndex].victims
        // } else {
        //     targetsText = "Unknown"
        // }
        // return !isTargetsOptionSelected 
        //     ?
        //     `${feature.properties.NAME}: ${feature.properties[property]} 
        //     ${formattedProperty}`
        //     :
        //     (   
        //         targetsText !== "Unknown" ?
        //         `${feature.properties.NAME}: ${number}`
        //         :
        //         targetsText
        //     )
    }
    
    svg
        .select(".x-axis")
        .style("transform", `translateY(${dimensions.height}px)`)
        .call(xAxis)
        .selectAll("text")
        .attr("y", 0)
        .attr("x", 9)
        .attr("dy", ".35em")
        .attr("transform", "rotate(90)")
        .style("text-anchor", "start")
        .style("font-size", xAxisFontSize !== undefined ? `${xAxisFontSize}px` : "")


    const yAxis = axisRight(yScale)
    svg
        .select(".y-axis")
        .style("transform", `translateX(${dimensions.width}px)`)
        .call(yAxis)

    // generates the "d" attribute of a path element
    const myLine = line()
    .x((value, index) => xScale(index))
    .y(yScale)

    // renders the path element and attaches the "d" 
    // attribute from line generator above
    svg
        .selectAll(".line")
        .data([values])
        .join("path")
        .attr("class", "line")
        .attr("d", myLine)
        .on('mouseenter', value => {
            select("#lineTooltip").style("display", "block")
            displayTooltip(value)
        })
        .on('mouseleave', value => select("#lineTooltip").style("display", "none"))
        .attr("fill", "none")
        .attr("stroke", "#DA0000")

    }, [lineGraphData, reverseData, yAxisMax, xAxisFontSize, dimensions])

    return (
    <article className="graph lineGraph">
        <h3 id="part4-1">Number of mass shootings recorded per year</h3>
        <div ref={wrapperRef}>
            <svg ref={svgRef}>
                <g className="x-axis" />
                <g className="y-axis" />
            </svg>
        </div>
        <Legend keys={["frequency"]} colors={["#DA0000"]} />
        <div id="lineTooltip" className="tooltip">
            <p id="pointInfo"></p>
        </div>
    </article>
    )
}

export default LineGraph